import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';

	import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public viewCtrl :ViewController, public loadingController:LoadingController, private alertCtrl: AlertController, public formBuilder: FormBuilder, public modalCtrl : ModalController, public storage: Storage, public events: Events) {


     this.slideOneForm = formBuilder.group({
			firstName: ['', Validators.compose([Validators.maxLength(10), Validators.pattern('^[0-9]{1,45}$'), Validators.required])],
			lastName: ['', Validators.compose([Validators.maxLength(10), Validators.pattern('^[0-9]{1,45}$'), Validators.required])]


	    });


     this.slideTwoForm = formBuilder.group({
      firstName: ['', Validators.compose([ Validators.required])],
      lastName: ['', Validators.compose([ Validators.required])],
      dni: ['', Validators.compose([Validators.maxLength(11), Validators.required])],
        email: ['', Validators.compose([Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/), Validators.required])]

              });

 


  }



  ionViewDidLoad() {

  this.mymodel = 'segment1';
    this.apiProvider.verificarLogin()
    .then(data => {    
      console.log(data);
     });

		/* 
		this.storage.get('usr_tok_datagym').then((value) => {
		value ? this.userLogged = true : this.userLogged = false
		console.log(value);
		}).catch(() => this.userLogged = false);
		*/
  
		  
  }

  	presentAlert(titulo,mensaje) {
	  let alert = this.alertCtrl.create({
	    title: titulo,
	    subTitle: mensaje,
	    buttons: ['Cerrar']
	  });
	  alert.present();
	}

serSocio() {

 this.mymodel = 'segment2';



  }

validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

recuperarContra() {
  let alert = this.alertCtrl.create({
    title: 'Recuperar Codigo de acceso',
    subTitle: 'Ingresa tu correo electronico para restablecer tu cuenta',
    inputs: [
      {
        name: 'email',
        placeholder: 'Username',
         type: 'email'
      }
    ],
    buttons: [
      {
        text: 'Cancelar',
        role: 'cancelar',
        handler: data => {
          console.log('Cancel clicked');
        }
      },
      {
        text: 'Recuperar',
        handler: data => {
      
           if(this.validateEmail(data.email)){
               console.log(data.email);
              let loading = this.loadingController.create({content : "cargando..."});
              loading.present();

              this.apiProvider.enviarContra({email:data.email})
              .then(data => {    
              console.log(data);
              loading.dismissAll(); 
              if(data.error===false){
              this.presentAlert('Exito!','Te hemos enviado el codigo de acceso al correo electronico');
              //this.closeModal();
              return true;
              }
              else{
              this.presentAlert('Ups!','Correo no registrado');
              return false;
              }
              });

            }else{
              this.presentAlert('Ups!', 'Email invalido');
              return false;
              
            }


            // logged in!

        
        }
      }
    ]
  });
  alert.present();
}


   doLogin() {

    let loading = this.loadingController.create({content : "cargando..."});
	loading.present();

    this.apiProvider.doLoginApi(this.slideOneForm.value)
    .then(data => {    
    

    	loading.dismissAll(); 

      if(data.data[0] &&(data.data[0].idUsuario  > 0)){

      	console.log(data);

        this.storage.set(`usr_tok_datagym`, data.data[0]);
        this.events.publish('userCreated', data.data[0]);
      	this.closeModal();

      }

      else{
      this.presentAlert('Ups!','Credenciales incorrectas');
      }
      
  
    });

  }





   registrarUsuario() {

    let loading = this.loadingController.create({content : "cargando..."});
  loading.present();

    this.apiProvider.registrarUsuario(this.slideTwoForm.value)
    .then(data => {    
        console.log(data);
      loading.dismissAll(); 

      if(data.data && data.data.insertId &&(data.data.insertId  > 0)){



        //this.storage.set(`usr_tok_datagym`, data.data[0]);
        //this.events.publish('userCreated', data.data[0]);
      this.slideTwoForm.reset()
        this.presentAlert('Exito!','Usuario creado correctamente. Tu numero de socio y codigo ser√° entregado en breve');
          this.mymodel = 'segment1';
        //this.closeModal();
      }

      else{
      this.presentAlert('Ups!','Ha ocurrido un error');
      }
      
  
    });

  }

  


  public closeModal(){
    this.viewCtrl.dismiss();
}



}



